"use client";
import { useState, useEffect } from "react";
import { UserPlus, Users, Check, X, Trash2, Share2 } from "lucide-react";
import { env } from "@/lib/env";
import s from "./FriendsTab.module.css";

interface FriendUser {
    id: string;
    email: string;
    name?: string;
}
interface Friendship {
    id: string;
    user1: FriendUser;
    user1Id: string;
    user2: FriendUser;
    user2Id: string;
    status: string;
    sharedCalendarIds: string[];
}
interface CalendarData {
    id: string;
    name: string;
    type: string;
}

function getUid(): string | null {
    const t =
        typeof window !== "undefined" ? localStorage.getItem("token") : null;
    if (!t) return null;
    try {
        return JSON.parse(atob(t)).sub;
    } catch {
        return null;
    }
}

export default function FriendsTab() {
    const [friends, setFriends] = useState<Friendship[]>([]);
    const [calendars, setCalendars] = useState<CalendarData[]>([]);
    const [loading, setLoading] = useState(true);

    // Add friend modal
    const [showAdd, setShowAdd] = useState(false);
    const [addEmail, setAddEmail] = useState("");
    const [addLoad, setAddLoad] = useState(false);
    const [addErr, setAddErr] = useState("");

    // Share calendar modal
    const [shareTarget, setShareTarget] = useState<Friendship | null>(null);

    const uid = getUid();

    useEffect(() => {
        load();
    }, []);

    const api = (path: string, opts?: RequestInit) =>
        fetch(`${env.API_URL}${path}`, {
            ...opts,
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json",
                ...opts?.headers,
            },
        });

    const load = async () => {
        setLoading(true);
        const [fr, cr] = await Promise.all([
            api("/api/friends"),
            api("/api/calendars"),
        ]);
        if (fr.ok) setFriends(await fr.json());
        if (cr.ok) setCalendars(await cr.json());
        setLoading(false);
    };

    const sendRequest = async () => {
        if (!addEmail) return;
        setAddLoad(true);
        setAddErr("");
        try {
            const r = await api("/api/friends/request", {
                method: "POST",
                body: JSON.stringify({ userId: addEmail }),
            });
            if (!r.ok)
                throw new Error((await r.json()).error || "Failed to send");
            setShowAdd(false);
            setAddEmail("");
            load();
        } catch (e) {
            setAddErr(e instanceof Error ? e.message : "Failed");
        } finally {
            setAddLoad(false);
        }
    };

    const accept = async (id: string) => {
        await api(`/api/friends/${id}/accept`, { method: "POST" });
        load();
    };

    const remove = async (id: string) => {
        if (
            !confirm(
                "Remove this friend? This will also remove all shared calendars.",
            )
        )
            return;
        await api(`/api/friends/${id}`, { method: "DELETE" });
        load();
    };

    const toggleCalendarShare = async (
        friendship: Friendship,
        calendarId: string,
    ) => {
        const friendId =
            friendship.user1Id === uid
                ? friendship.user2Id
                : friendship.user1Id;
        const isShared = friendship.sharedCalendarIds.includes(calendarId);
        await api("/api/friends/share-calendar", {
            method: "POST",
            body: JSON.stringify({ friendId, calendarId, share: !isShared }),
        });
        load();
    };

    const getFriend = (f: Friendship) =>
        f.user1Id === uid ? f.user2 : f.user1;

    const accepted = friends.filter((f) => f.status === "accepted");
    const incoming = friends.filter(
        (f) => f.status === "pending" && f.user2Id === uid,
    );
    const outgoing = friends.filter(
        (f) => f.status === "pending" && f.user1Id === uid,
    );

    if (loading)
        return (
            <div className={s.loading}>
                <div className={s.spinner} />
                <span>Loading‚Ä¶</span>
            </div>
        );

    return (
        <div className={s.page}>
            {/* ‚îÄ‚îÄ Header ‚îÄ‚îÄ */}
            <div className={s.pageHeader}>
                <h1 className={s.pageTitle}>Friends</h1>
                <button
                    className={`${s.btn} ${s.btnPrimary}`}
                    onClick={() => setShowAdd(true)}
                >
                    <UserPlus size={14} /> Add Friend
                </button>
            </div>

            {/* ‚îÄ‚îÄ Incoming Requests ‚îÄ‚îÄ */}
            {incoming.length > 0 && (
                <div className={s.section}>
                    <div className={s.sectionTitle}>
                        Friend Requests ¬∑ {incoming.length}
                    </div>
                    <div className={s.list}>
                        {incoming.map((f) => {
                            const fr = getFriend(f);
                            return (
                                <div key={f.id} className={s.row}>
                                    <div className={s.rowInfo}>
                                        <div className={s.rowName}>
                                            {fr.name || fr.email}
                                        </div>
                                        <div className={s.rowEmail}>
                                            {fr.email}
                                        </div>
                                    </div>
                                    <div className={s.rowActions}>
                                        <button
                                            className={`${s.btn} ${s.btnSuccess} ${s.btnSm}`}
                                            onClick={() => accept(f.id)}
                                        >
                                            <Check size={12} /> Accept
                                        </button>
                                        <button
                                            className={`${s.btn} ${s.btnDanger} ${s.btnSm} ${s.btnIcon}`}
                                            onClick={() => remove(f.id)}
                                        >
                                            <X size={13} />
                                        </button>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}

            {/* ‚îÄ‚îÄ Friends ‚îÄ‚îÄ */}
            <div className={s.section}>
                <div className={s.sectionTitle}>
                    Friends ¬∑ {accepted.length}
                </div>
                {accepted.length === 0 ? (
                    <div className={s.empty}>
                        <Users size={38} className={s.emptyIcon} />
                        <span>No friends yet ‚Äî add someone to get started</span>
                    </div>
                ) : (
                    <div className={s.list}>
                        {accepted.map((f) => {
                            const fr = getFriend(f);
                            const sharedCount =
                                f.sharedCalendarIds?.length || 0;
                            return (
                                <div key={f.id} className={s.row}>
                                    <div className={s.rowInfo}>
                                        <div className={s.rowName}>
                                            {fr.name || fr.email}
                                        </div>
                                        <div className={s.rowEmail}>
                                            {fr.email}
                                        </div>
                                        <div className={s.rowMeta}>
                                            {sharedCount > 0 ? (
                                                <span
                                                    className={`${s.badge} ${s.badgeGreen}`}
                                                >
                                                    <Check size={9} />{" "}
                                                    {sharedCount} calendar
                                                    {sharedCount !== 1
                                                        ? "s"
                                                        : ""}{" "}
                                                    shared
                                                </span>
                                            ) : (
                                                <span
                                                    className={`${s.badge} ${s.badgeMuted}`}
                                                >
                                                    Not sharing
                                                </span>
                                            )}
                                        </div>
                                    </div>
                                    <div className={s.rowActions}>
                                        <button
                                            className={`${s.btn} ${s.btnSecondary} ${s.btnSm}`}
                                            onClick={() => setShareTarget(f)}
                                            title="Share calendars"
                                        >
                                            <Share2 size={12} /> Share
                                        </button>
                                        <button
                                            className={`${s.btn} ${s.btnDanger} ${s.btnSm} ${s.btnIcon}`}
                                            onClick={() => remove(f.id)}
                                        >
                                            <Trash2 size={13} />
                                        </button>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>

            {/* ‚îÄ‚îÄ Outgoing Requests ‚îÄ‚îÄ */}
            {outgoing.length > 0 && (
                <div className={s.section}>
                    <div className={s.sectionTitle}>
                        Sent Requests ¬∑ {outgoing.length}
                    </div>
                    <div className={s.list}>
                        {outgoing.map((f) => {
                            const fr = getFriend(f);
                            return (
                                <div key={f.id} className={s.row}>
                                    <div className={s.rowInfo}>
                                        <div className={s.rowName}>
                                            {fr.name || fr.email}
                                        </div>
                                        <div className={s.rowEmail}>
                                            {fr.email}
                                        </div>
                                        <div className={s.rowMeta}>
                                            <span
                                                className={`${s.badge} ${s.badgeYellow}`}
                                            >
                                                Pending
                                            </span>
                                        </div>
                                    </div>
                                    <button
                                        className={`${s.btn} ${s.btnSecondary} ${s.btnSm}`}
                                        onClick={() => remove(f.id)}
                                    >
                                        Cancel
                                    </button>
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}

            {/* ‚îÄ‚îÄ Add Friend Modal ‚îÄ‚îÄ */}
            {showAdd && (
                <div
                    className={s.overlay}
                    onClick={() => {
                        setShowAdd(false);
                        setAddErr("");
                    }}
                >
                    <div
                        className={s.modal}
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className={s.modalHeader}>
                            <span className={s.modalTitle}>Add Friend</span>
                            <button
                                className={s.closeBtn}
                                onClick={() => setShowAdd(false)}
                            >
                                <X size={18} />
                            </button>
                        </div>
                        <div className={s.formStack}>
                            <div>
                                <label className={s.fieldLabel}>
                                    Email Address
                                </label>
                                <input
                                    className={s.input}
                                    type="email"
                                    value={addEmail}
                                    onChange={(e) =>
                                        setAddEmail(e.target.value)
                                    }
                                    onKeyDown={(e) =>
                                        e.key === "Enter" && sendRequest()
                                    }
                                    placeholder="friend@example.com"
                                    autoFocus
                                />
                            </div>
                            {addErr && <div className={s.error}>{addErr}</div>}
                            <div className={s.formRow}>
                                <button
                                    className={`${s.btn} ${s.btnPrimary}`}
                                    style={{ flex: 1 }}
                                    onClick={sendRequest}
                                    disabled={addLoad || !addEmail}
                                >
                                    {addLoad ? "Sending‚Ä¶" : "Send Request"}
                                </button>
                                <button
                                    className={`${s.btn} ${s.btnSecondary}`}
                                    onClick={() => {
                                        setShowAdd(false);
                                        setAddErr("");
                                    }}
                                >
                                    Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/* ‚îÄ‚îÄ Share Calendars Modal ‚îÄ‚îÄ */}
            {shareTarget && (
                <div className={s.overlay} onClick={() => setShareTarget(null)}>
                    <div
                        className={s.modal}
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className={s.modalHeader}>
                            <span className={s.modalTitle}>
                                Share with{" "}
                                {getFriend(shareTarget).name ||
                                    getFriend(shareTarget).email}
                            </span>
                            <button
                                className={s.closeBtn}
                                onClick={() => setShareTarget(null)}
                            >
                                <X size={18} />
                            </button>
                        </div>
                        <p className={s.modalSub}>
                            Toggle which calendars are shared. Your friend gets
                            a live ICS subscription link.
                        </p>
                        {calendars.length === 0 ? (
                            <p
                                style={{
                                    color: "var(--text-muted)",
                                    fontSize: "0.875rem",
                                }}
                            >
                                Import a calendar first before sharing.
                            </p>
                        ) : (
                            <div className={s.shareList}>
                                {calendars.map((cal) => {
                                    const isShared =
                                        shareTarget.sharedCalendarIds?.includes(
                                            cal.id,
                                        );
                                    return (
                                        <div
                                            key={cal.id}
                                            className={s.shareRow}
                                            onClick={() =>
                                                toggleCalendarShare(
                                                    shareTarget,
                                                    cal.id,
                                                )
                                            }
                                        >
                                            <span className={s.shareRowName}>
                                                {cal.name}
                                            </span>
                                            {isShared ? (
                                                <span
                                                    className={`${s.badge} ${s.badgeGreen}`}
                                                >
                                                    <Check size={9} /> Shared
                                                </span>
                                            ) : (
                                                <span
                                                    className={`${s.badge} ${s.badgeMuted}`}
                                                >
                                                    Not shared
                                                </span>
                                            )}
                                        </div>
                                    );
                                })}
                            </div>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}
